/* Adding transform to the X */
.logo-group {
    position: fixed;
      top: 50%;
      left: 50%;
      margin-right: -50%;
      transform: translate(-50%, -50%);
      background-color: green;
    transition: all 0.5s ease-out;
    transform: rotate(180deg) translate(0px, 5px);
}

.logo-group .btn-line {
    position: absolute;
    width: 28px;
    height: 3px;
    margin: 0 0 5px 0;
}


/* Styling the three lines
    to make it an X */


.logo-group .btn-line:nth-child(4){
    /* Bot left */
    transform-origin: left;
    animation: h_b_left 2s ease-in-out 0s infinite;
    transform: translate(28px,-21px) rotatez(90deg) scaleX(0.4);
}

.logo-group .btn-line:nth-child(5) {
    /* Bot right*/
    animation: h_b_right 2s ease-in-out 0s infinite;
    transform-origin: right;
    transform: translate(-13px,-27px) rotatez(0deg) scaleX(1);
} 
.logo-group .btn-line:nth-child(3) {
    /* top right */
    animation: h_t_right 2s ease-in-out 0s infinite;
    transform-origin: right;
    transform: translate(-21px,14px) rotatez(0deg) scaleX(0.4);
}

.logo-group .btn-line:nth-child(1){
    /* top left */
    transform-origin: left;
    animation: h_t_left 2s ease-in-out 0s infinite;
    transform: translate(7px,14px)  rotatez(0deg) scaleX(0.8);
}

.logo-group .btn-line:nth-child(2) {
    /* vertical right */
    animation: h_v_right 2s ease-in-out 0s infinite;
    transform: rotateZ(90deg) translate(-2px,17px) scaleX(1.2);
} 
.logo-group .btn-line:nth-child(6) {
    /* vertical left */
    animation: h_v_left 2s ease-in-out 0s infinite;
    transform: rotateZ(90deg) translate(4px,-14px) scaleX(0.8);
}


/* Inner */
.logo-group .btn-line:nth-child(7) {
    /* inner left to shrink */
    transform-origin: left;
    animation: h_in_left 2s ease-in-out 0s infinite;
    transform: rotateZ(30deg) translate(30px,-2px) scaleX(0);
}
.logo-group .btn-line:nth-child(8) {
    /* inner right to top //*/
    transform-origin: right;
    animation: h_in_right 2s ease-in-out 0s infinite;
    transform: rotateZ(0deg) translate(1px,14px) scaleX(1.2);
}
.logo-group .btn-line:nth-child(9) {
    /* inner center to left */
    
    animation: h_in_center 2s ease-in-out 0s infinite;
    transform-origin: right;
    transform:rotateZ(90deg) translate(15px,0px) scaleX(1.2);
}


@keyframes h_in_left {
    0% {transform: rotateZ(30deg) translate(12px,-6px) scaleX(0);
    }

    25% {transform: rotateZ(30deg) translate(12px,-6px) scaleX(0);
    }
    50% {transform: rotateZ(30deg) translate(12px,-6px) scaleX(1);
    }
    75% {
        transform: rotateZ(30deg) translate(30px,-2px) scaleX(0);
    }
    100%{
        transform: rotateZ(30deg) translate(30px,-2px) scaleX(0);
    }
}

@keyframes h_in_center {
    0% {transform: rotateZ(90deg) translate(1px,14px) scaleX(0);
    }

    25% {transform: rotateZ(90deg) translate(1px,14px) scaleX(0.5);
    }
    50% {transform: rotateZ(90deg) translate(1px,12px) scaleX(1);
    }
    75% {
        transform: rotateZ(90deg) translate(15px,0px) scaleX(1.2);
    }
    100%{
        transform: rotateZ(90deg) translate(15px,0px) scaleX(1.2);
    }
}

@keyframes h_in_right {
    0% {
        transform: rotateZ(-30deg) translate(-11px,-7px) scaleX(0);
    }

    25% {transform: rotateZ(-30deg) translate(-11px,-7px) scaleX(0);
    }
    50% {
        
        transform: rotateZ(-30deg) translate(-11px,-7px) scaleX(1);
    }
    75% {
        
    transform: rotateZ(0deg) translate(1px,14px) scaleX(1.2);
    }
    100%{
        
    transform: rotateZ(0deg) translate(1px,14px) scaleX(1.2);
    }
}


/* Hexagon animate */
@keyframes h_v_left {
    0% {
        transform: rotateZ(90deg) translate(0px,-0px) scaleX(0);
    }

    25% {
        transform: rotateZ(90deg) translate(0px,0px) scaleX(1);
    }
    50% {
        transform: rotateZ(90deg) translate(0px,-22px) scaleX(1);
    }
    75% {
        
    transform: rotateZ(90deg) translate(4px,-14px) scaleX(0.70);
    }
    100%{
        transform: rotateZ(90deg) translate(4px,-14px) scaleX(0.7);
    }
}

@keyframes h_v_right {
    0% {
        transform: rotateZ(90deg) translate(0px,0px) scaleX(0);
    }

    25% {
        transform: rotateZ(90deg) translate(0px,0px) scaleX(1);
    }
    50% {
        transform: rotateZ(90deg) translate(0px,23px) scaleX(1);
    }
    75% {
        
    transform: rotateZ(90deg) translate(-2px,17px) scaleX(1.2);
    }
    100%{
        transform: rotateZ(90deg) translate(-2px,17px) scaleX(1.2);
    }
}

